import React from "react";
import {useAppDispatch, useAppSelector} from "../library/state";
import {classMap} from "../library/utils";
import {getUxComparePools, getUxCompareModal, setCompareModal} from "../library/ux";
import {getOptionState} from "../library/option";
import {Pools} from "./Pools";

export const CompareModal = () => {

    // APP State

    const dispatch = useAppDispatch()
    const compareModal: UxState['compareModal'] = useAppSelector(getUxCompareModal)
    const compareItems: UxState['compareItems'] = useAppSelector(getUxComparePools)
    const options: OptionState = useAppSelector(getOptionState)

    // Helpers

    return compareItems ? (
        <>
            {compareItems?.length ? <button
                className={classMap.compareButton}
                onClick={() => dispatch(setCompareModal(!compareModal))}>
                {options.label_compare_view} ({compareItems.length})
            </button> : null}
            {compareModal ? (
                <div className={classMap.assetModal}>
                    <div className={classMap.assetModalHeader}>
                        <h2 className={classMap.assetModalTitle}>{options.label_compare_view} ({compareItems.length})</h2>
                        <button className={classMap.assetModalClose} onClick={() => dispatch(setCompareModal(null))}>
                            x
                        </button>
                    </div>
                    <div className={classMap.compareModalBody}>
                        <Pools
                            perPage={0}
                            pools={compareItems}
                            notFound={options.label_compare_no_items}
                        />
                    </div>
                </div>
            ) : null}
        </>
    ) : null
}

